<template lang="pug">
    div.rankList
        template(v-if="isLoaded")
            div.total
                i.icon.iconfont &#xe669;
                span(v-text="`${count.total_num}${appInit.ticket_name}`")
                span.btn-rank(@click="btnRank") 总榜
            mt-loadmore.loadmore-rank(
                :top-method="loadTop"
                :bottom-method="loadBottom"
                :bottom-all-loaded="allLoaded"
                :autoFill="autoFill"
                ref="loadmore"
                @top-status-change="handleTopChange"
                @bottom-status-change="handleBottomChange"
            )
                mint-loadmore-top(:topStatus="topStatus" slot="top")
                //- 前三名
                template(v-if="list && list.length > 0")
                    div.rank-list-top
                        div.rank-item-top.top-two
                            div.rank-item-top-inner(v-if="list[1]")
                                router-link.head-image(:to="{ path: '/user/others', query: { to_user_id: list[1].user_id }}")
                                    div.avatar-box
                                    img.avatar(v-lazy="list[1] && list[1].head_image")
                                div.item-info
                                    div.nick-name 
                                        span(v-text="list[1] && list[1].nick_name")
                                        div.praise
                                            i.icon.iconfont.sex-man(v-if="list[1] && list[1].sex == 1") &#xe661;
                                            i.icon.iconfont.sex-woman(v-if="list[1] && list[1].sex == 2") &#xe662;
                                            img.icon-level(:src="iconLevel[1]")
                                    div.income(v-text="`消费${list[1] && list[1].num}${appInit.ticket_name}`" )
                        div.rank-item-top.top-one
                            div.rank-item-top-inner
                                router-link.head-image(:to="{ path: '/user/others', query: { to_user_id: list[0].user_id }}")
                                    div.avatar-box(:style="`background:url(${background});background-size: contain`")
                                    img.avatar(v-lazy="list[0].head_image")
                                div.item-info
                                    div.nick-name 
                                        span(v-text="list[0].nick_name")
                                        div.praise
                                            i.icon.iconfont.sex-man(v-if="list[0].sex == 1") &#xe661;
                                            i.icon.iconfont.sex-woman(v-if="list[0].sex == 2") &#xe662;
                                            img.icon-level(:src="iconLevel[0]")
                                    div.income(v-text="`消费${list[0].num}${appInit.ticket_name}`"  )
                        div.rank-item-top.top-three
                            div.rank-item-top-inner(v-if="list[2]")
                                router-link.head-image(:to="{ path: '/user/others', query: { to_user_id: list[2].user_id }}")
                                    div.avatar-box
                                    img.avatar(v-lazy="list[2] && list[2].head_image")
                                div.item-info
                                    div.nick-name 
                                        span(v-text="list[2] && list[2].nick_name")
                                        div.praise
                                            i.icon.iconfont.sex-man(v-if="list[2] && list[2].sex == 1") &#xe661;
                                            i.icon.iconfont.sex-woman(v-if="list[2] && list[2].sex == 2") &#xe662;
                                            img.icon-level(:src="iconLevel[2]")
                                    div.income(v-text="`消费${list[2] && list[2].num}${appInit.ticket_name}`" )
                    //- 三名以后
                    ul.rank-list-other
                        li.rank-item(v-for="(item, index) in list" v-if="index > 2")
                            div.rank-number(v-text="index + 1")
                            router-link.head-image(:to="{ path: '/user/others', query: { to_user_id: item.user_id }}") 
                                img(v-lazy="item.head_image")
                            div.user-information
                                div.user-name 
                                    span(v-text="item.nick_name")
                                    div.praise
                                        i.icon.iconfont.sex-man(v-if="item.sex == 1") &#xe661;
                                        i.icon.iconfont.sex-woman(v-if="item.sex == 2") &#xe662;
                                        img.icon-level(:src="iconLevel[index]")
                                div.user-income(v-text="`消费${item.num}${appInit.ticket_name}`" )
                        <div v-if="allLoaded" class="no-more-data">无更多数据</div>
                template(v-else)
                    null-data
                mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")        
        div.mt-spinner-box(v-else)
                mt-spinner(
                    :type="0"
                    color="#333"
                )


</template>
<script>
import nullData from '@/components/nullData'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import { mapGetters, mapActions } from 'vuex'
import { Indicator, Toast } from 'mint-ui'
export default {
    name: 'userDedicate',
    components: {
        nullData,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    beforeRouteEnter(to, from, next) {   // 路由进来之前
        next(vm => {
            vm.setFooterBarActiveName('userDedicate');
            $(".page-content").scrollTop(0);
        });
    },
    data() {
        return {
            isLoaded: false,
            count: '',
            list: '',
            page: 2,
            has_next: '',
            autoFill: false,
            allLoaded: false,
            topStatus: '',
            bottomStatus: '',
            background: require('@/assets/img/crown-no1.png'),
            type: '',
        }
    },
    created() {
        this.getData();
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        //获取数据
        getData(isRefresh = false) {
            let self = this;
            self.to_user_id = self.$route.query.to_user_id;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_contribution' : self.api.userContribution();
            self.axios.get(axiosUrl, {
                params: {
                    user_id: self.to_user_id
                }
            }).then(res => {
                self.isLoaded = true;
                self.count = res.data;
                self.list = res.data.list;
                self.page = 2;
                self.has_next = res.data.has_next;

                self.$nextTick(function() {
                    isRefresh && this.$refs.loadmore.onTopLoaded();

                    self.$refs.loadmore.onTopLoaded();
                });
            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
        },
        loadTop(id) {
            let self = this;
            self.getData(true);
        },
        loadBottom() {
            let self = this;
            if (self.has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_contribution' : self.api.userContribution();
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page
                    }
                }).then(res => {
                    setTimeout(() => {
                        if (res.data.status == 1) {
                            self.has_next = res.data.has_next;
                            self.page = self.page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.list.push(res.data.list[i]);
                                }
                            }
                        }
                        else {
                            Toast(res.data.error || '操作失败');
                        }
                        self.$nextTick(function() {
                            self.$refs.loadmore.onBottomLoaded();
                        });
                    }, 500)
                })
                    .catch(err => {
                        Toast(err || '网络异常，请求失败');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                return false;
            }
        },
        //跳转到消费榜
        btnRank() {
            this.$router.push('/rank/index');
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        list() {
            this.allLoaded = !Boolean(this.has_next);
        }
    },
    computed: {
        ...mapGetters({
            appInit: 'getAppInit'
        }),
        iconLevel() {
            let arr_user_level = [];
            for (let val of this.list) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.user_level}.png`));
            }
            return arr_user_level;
        }
    }

}
</script>
<style lang="less">
@import '../../assets/css/variable.less';
.rankList {
    padding: .5rem .5rem 0;
    height: 100%;
    .total {
        text-align: center;
        margin-bottom: .5rem;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        .iconfont {
            font-size: 1rem;
            vertical-align: middle;
        }
        span {
            font-size: .6rem;
        }
        .btn-rank {
            position: absolute;
            right: 0;
            font-size: .7rem;
        }
    }
    .rank-list-top {
        display: flex;
        justify-content: space-around;
        width: 100%;
        height: 7.75rem;
        background: url(../../assets/img/bottomBase.png) no-repeat;
        background-size: contain;
        background-position: bottom;
        .rank-item-top {
            width: 33.3%;
            display: flex;
            justify-content: flex-end;
            flex-direction: column;

            .rank-item-top-inner {
                width: 100%;
                text-align: center;
                .head-image {
                    margin: 0 auto;
                }
                .item-info {
                    padding: .5rem;
                    text-align: center;
                    white-space: nowrap;
                    .nick-name {
                        font-size: .65rem;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        .iconfont {
                            margin: 0 .25rem;
                            font-size: .75rem;
                        }
                        .sex-woman {
                            color: #ff71bb;
                        }
                        .sex-man {
                            color: #3fa2ff;
                        }
                        span {
                            display: block;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                        }
                        .praise {
                            display: flex;
                            align-items: center;
                            .iconfont {
                                font-size: .75rem;
                                margin-right: .25rem;
                            }
                            img {
                                width: 1.3rem;
                                height: .63rem;
                                vertical-align: middle;
                            }
                        }
                    }
                    .income {
                        font-size: .5rem;
                        color: @fc-light;
                    }
                }
            }
        }
        .top-two {
            .head-image {
                position: relative;
                width: 4rem;
                height: 3.15rem;
                display: flex;
                justify-content: center;


                .avatar-box {
                    position: absolute;
                    z-index: 2;
                    width: 4rem;
                    height: 3.15rem;
                    left: 50%;
                    margin-left: -2rem;
                    background: url(../../assets/img/crown-no2.png) no-repeat;
                    background-size: contain;
                }
                .avatar {
                    width: 2.2rem;
                    height: 2.2rem;
                    position: absolute;
                    top: .6rem;
                    border-radius: 2.5rem;
                    z-index: 1;
                    left: 50%;
                    margin-left: -1.1rem;
                }
            }
        }
    }
    .top-three {
        .head-image {
            position: relative;
            width: 4rem;
            height: 3.15rem;
            display: flex;
            justify-content: center;
            .avatar-box {
                position: absolute;
                z-index: 2;
                width: 4rem;
                height: 3.15rem;
                background: url(../../assets/img/crown-no3.png) no-repeat;
                background-size: contain;
                left: 50%;
                margin-left: -2rem;
            }
            .avatar {
                width: 2.2rem;
                height: 2.2rem;
                position: absolute;
                top: .6rem;
                border-radius: 2.5rem;
                z-index: 1;
                left: 50%;
                margin-left: -1.1rem;
            }
        }
    }
    .top-one {
        .rank-item-top-inner {
            // padding-bottom: .5rem;
            height: 100%;
            text-align: center;
            .head-image {
                position: relative;
                width: 4.25rem;
                height: 4.5rem;
                display: flex;
                justify-content: center;
                .avatar-box {
                    position: absolute;
                    z-index: 2;
                    width: 4.25rem;
                    height: 4.5rem;
                    left: 50%;
                    margin-left: -2.125rem;
                }
                .avatar {
                    width: 3rem;
                    height: 3rem;
                    position: absolute;
                    top: 1.1rem;
                    border-radius: 2.5rem;
                    z-index: 1;
                    left: 50%;
                    margin-left: -1.5rem;
                }
            }
        }
    }
    .rank-list-other {
        padding: 0 0 0 .5rem;
        background: #fff;
        .rank-item {
            height: 3.25rem;
            width: 100%; // line-height: 3.25rem;
            display: flex;
            align-items: center;
            .rank-number {
                width: .65rem;
                height: .65rem;
                background: #fee204;
                text-align: center;
                line-height: .7rem;
                font-size: .5rem;
                border-radius: 15%;
                flex-shrink: 0;
            }
            .head-image {
                height: 2rem;
                width: 2rem;
                border-radius: 2.5rem;
                overflow: hidden;
                margin-left: .5rem;
                flex-shrink: 0;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .user-information {
                border-bottom: 1px solid #e6e8f1;
                width: 100%;
                height: 3.25rem;
                margin-left: .45rem;
                .user-name {
                    width: 50%;
                    font-size: .65rem;
                    padding: .825rem 0 0 0;
                    display: flex;
                    align-items: center;
                    .iconfont {
                        margin: 0 .25rem;
                        font-size: .75rem;
                    }
                    .sex-woman {
                        color: #ff71bb;
                    }
                    .sex-man {
                        color: #3fa2ff;
                    }
                    span {
                        display: block;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .praise {
                        display: flex;
                        align-items: center;
                        .iconfont {
                            font-size: .75rem;
                            margin-right: .25rem;
                        }
                        img {
                            width: 1.3rem;
                            height: .63rem;
                            vertical-align: middle;
                        }
                    }
                }
                .user-income {
                    font-size: .5rem;
                    color: @fc-light;
                }
            }
        }
    }
}
</style>





